import React from "react";
import { Button, Dialog, DialogActions, DialogContent, DialogTitle, useMediaQuery } from "@material-ui/core";
import { useTheme } from "@material-ui/core/styles";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import categoryList from "../../JSONFiles/categoryList";

const phoneRegExp = /^(?:(?:\+|0{0,2})91(\s*[-]\s*)?|[0]?)?[56789]\d{9}$/;
const products = ["Product 1", "Product 2", "Product 3", "Product 4"];

const SignupSchema = yup.object().shape({
  FullName: yup
    .string()
    .required("Name Field Mandatory")
    .min(3, "Name should be more than 3 characters")
    .max(20, "Name should be less than 20 characters"),
  contactNumber: yup.string().matches(phoneRegExp, "Phone number is not valid"),
  message: yup.string().required().max(10, "Message should be less than 100 characters"),
  product: yup.string().required("Please select a product").oneOf(products),
  // age: yup.number().required().positive().integer(),
  // website: yup.string().url(),
});

function BannerDialog(props) {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({
    resolver: yupResolver(SignupSchema),
  });
  const onSubmit = (data) => alert(JSON.stringify(data));

  let pr = [];

  for (let i = 0; i < categoryList.length; i++) {
    for (let j = 0; j < categoryList[i].productList.length; j++) {
      pr.push(categoryList[i].productList[j]);
    }
  }

  console.log(pr);

  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down("sm"));

  return (
    <div>
      <Dialog
        fullScreen={fullScreen}
        open={props.onOpen}
        onClose={props.onClose}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title" style={{ textAlign: "center" }}>
          Enquiry Form
        </DialogTitle>
        <hr className="enquiryhr" />
        <DialogContent>
          <form class="ui form" onSubmit={handleSubmit(onSubmit)} style={{ width: "300px" }}>
            <div class="EnquiryField">
              <div>
                <label className="enquiryLabelFs14">Your Name</label>
              </div>

              <input className="enquiryInput" {...register("FullName")} type="text" />
              <p className="errorRed">{errors.FullName?.message}</p>
            </div>

            <div class="EnquiryField">
              <label className="enquiryLabelFs14">Contact Number</label>
              <input className="enquiryInput" {...register("contactNumber")} type="text" />
              <p className="errorRed">{errors.contactNumber?.message}</p>
            </div>
            <div class="EnquiryField">
              <label className="enquiryLabelFs14">Type Your Message Here</label>
              <textarea className="enquiryInput" {...register("message")} rows="2"></textarea>
            </div>
            <div class="EnquiryField">
              <label className="enquiryLabelFs14">Product List</label>
              <select className="enquiryDropdown" {...register("product")}>
                {pr.map((products) => (
                  <option value="">{products.productName}</option>
                ))}
              </select>
            </div>
            <Button
              variant="contained"
              type="submit"
              size="small"
              style={{
                backgroundColor: "#793fa5",
                color: "#fff",
                width: "100%",
                marginBottom: "15px",
                fontSize: "13px",
              }}
            >
              SUBMIT
            </Button>
            <Button
              autoFocus
              onClick={props.onClose}
              size="small"
              style={{
                backgroundColor: "#01975f",
                color: "#fff",
                width: "100%",
                marginBottom: "20px",
                fontSize: "13px",
              }}
            >
              Close
            </Button>
          </form>
        </DialogContent>
      </Dialog>
    </div>
  );
}

export default BannerDialog;

